* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}

:root {
  --theme-blue: #0083b3;
}

p {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: black;
  font-size: 16px;
}

.main-container {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
}

.emptyList-wrap {
  display: flex;
  justify-content: center;
}

.emptyList-wrap img {
  max-width: 500px;
  width: 100%;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}

.grid-4 {
  /* display: flex; */
  /* margin-top: 20px; */
  /* width: 100%; */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  /* margin: 2rem; */

}

.grid-1-3 {
  display: grid;
  grid-template-columns: 25% auto;
  grid-gap: 3rem;
}

.grid-3-1 {
  display: grid;
  grid-template-columns: auto 25%;
  grid-gap: 3rem;
}

.grid-1-2 {
  display: grid;
  grid-template-columns: 33% auto;
  grid-gap: 3rem;
}

.section {
  margin: 10px 10vw;
}

.section-heading h1 {
  font-size: 40px;
  padding: 10px 0px;
  font-family: 'Maven Pro', sans-serif;
  font-weight: 800;
  position: relative;
  margin-bottom: 40px;
} 
.section-heading h1::before{
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 4px;
  border-radius: 2px;
  width: 120px;
  background: rgb(247, 9, 29);
}


.page-heading h1{
  color: #000000;
  font-size: 40px;
  font-weight: 800;
  position: relative;
  font-family: 'Maven Pro', sans-serif;
  margin-bottom: 60px;
}

.page-heading h1::before{
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 4px;
  border-radius: 2px;
  width: 50px;
  background: rgb(58, 141, 235);
}






