/* Banner */
/* .................... */
#head {
  height: 70vh;
  max-height: 800px;
  width: 100%;
  display: flex;
  background-repeat: space;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: overlay;
  padding: 0 10vw;
  z-index: 0;

}

.banner-content {
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 20px;

}

.banner-content h1 {
  /* color: var(--theme-blue); */
  color: #e7f6f9;
  font-size: 74px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 700;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;
}

.banner-content h2 {
  color: #fff3c3;
  font-size: 24px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 700;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;

}


.home-video {
  width: 100%;
  background: #151414;
  margin-top: 20px;

}

.home-video video {
  width: 100vw;

}

.about-section {
  margin: 20px 0px;
}

.about-section p {
  font-size: 24px;
  font-family: 'Jost', sans-serif;
  font-weight: 600;

}


.team-section{
  margin-bottom: 40px;
}
.team-cards {
  text-align: center;
  border-radius: 50px;
  padding: 40px;
  background: #f3f2f2;
  position: relative;
  overflow: hidden;
  text-align: center;

}

.team-cards:before {
  position: absolute;
  content: '';
  left: 0px;
  bottom: 0px;
  width: 0%;
  height: 0%;
  border-radius: 50px;
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.30);
  transition: all 0.4s ease;
  background-image: linear-gradient(to right, #3c70a4 0%, #64b2cd 100%);
}

.team-cards:hover:before {
  height: 100%;
  width: 100%;
}

.team-cards:hover .team-card-image{
  padding: 0;
}

.team-card-image {
  position: relative;
  max-width: 210px;
  max-height: 210px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 50%;
  padding: 15px;
  transition: all 0.5s ease;
  box-shadow: inset 0px 0px 20px rgba(0, 0, 0, 0.20);
}

.team-card-image img{
  border-radius: 50%;
  transition: all 500ms ease;
  width: 100%;
}

.team-card-desc {
  position: relative;
  color: white;
}




.team-card-desc  h2{
  color: rgb(0, 0, 0);
  font-size: 22px;
}

.team-card-desc  h3{
  color: rgb(0, 0, 0);
  font-size: 18px;
}
.team-cards:hover .team-card-desc  h3{
  color: rgb(255, 255, 255);
}
.team-cards:hover .team-card-desc  h2{
  color: rgb(255, 255, 255);
}

.card-item {
  text-align: center;
  box-shadow: 0px 5px 5px 0px rgba(27, 27, 27, 0.1);
  padding: 1rem;
  border-radius: 10px;

}
.card-item-caption{
  padding: 10px 0px;
}

.card-item-caption h2{
  font-size: 22px;
}



.card-item-image {
  width: 100%;
  overflow: hidden;
  border-radius: 10px;


}

.card-item-image img {
  width: 100%;
  border-radius: 10px;
  transition: transform 0.5s ease;
}

.card-item-image img:hover {
  transform: scale(1.1);
  /* box-shadow: 0 0 11px rgba(33,33,33,.5);  */
}








.home-card-item {
  text-align: center;
  box-shadow: 0px 5px 5px 0px rgba(27, 27, 27, 0.1);
  padding: 1rem;
  border-radius: 10px;

}
.home-card-item-image {
  width: 100%;
  overflow: hidden;
  border-radius: 10px;

}
.home-card-item-caption{
  padding: 10px 0px;
}

.home-card-item-caption h2{
  font-size: 18px;
}

.home-card-item-image img {
  width: 100%;
  border-radius: 10px;
  transition: transform 0.5s ease;
}

.home-card-item-image img:hover {
  transform: scale(1.1);
  /* box-shadow: 0 0 11px rgba(33,33,33,.5);  */
}





.recent-events-item img {
  width: 100%;
}

.events-detalis img {
