.footer-section {
  background: #151414;
  position: relative;
}

.footer-container {
  margin: 0px 10vw;
}


.footer-content {
  position: relative;
  z-index: 2;
  padding: 50px 0px;

}


.footer-logo {
  /* margin-bottom: 30px; */
}

.footer-logo img {
  max-width: 100px;
}

.footer-text p {
  margin-bottom: 15px;
  font-size: 1rem;
  color: #7e7e7e;
  line-height: 28px;
}


.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
  font-family: 'Maven Pro', sans-serif;
}

.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background: var(--theme-blue);
}

.footer-widget ul li {
  display: inline-block;
  width: 50%;
  margin-bottom: 12px;
}

.footer-widget ul li a:hover {
  color: var(--theme-blue);
}

.footer-widget ul li a {
