}

.footer-menu {
  text-align: right;

}

.footer-menu li {
  display: inline-block;
  margin-left: 20px;
  text-align: right;

}

.footer-menu li:hover a {
  color: var(--theme-blue);
}

.footer-menu li a {
  font-size: 14px;
  color: #878787;
}

.socialLinks {
  display: flex;
  width: 100%;
}

.socialLinks ul {
  display: flex;

}


.socialLinks ul li a i {
  margin: 5px;
  width: 40px;
  font-size: 24px;
  height: 40px;
  background: #fafafa;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.socialLinks ul li a .fa-facebook-f {
  color: #4267b2;
}


.socialLinks ul li a .fa-instagram {
  color: #dd5044;
}

.socialLinks ul li a .fa-youtube {
  color: #dd5044;
}


.socialLinks ul li a:hover i {
  color: #fff;
}

.socialLinks ul li a:hover .fa-facebook-f {
  background: #4267b2;
  cursor: pointer;
}

.socialLinks ul li a:hover .fa-youtube {
  background: #f21d1d;
  cursor: pointer;
}

.socialLinks ul li a:hover .fa-instagram {
  background: #dd5044;
  cursor: pointer;
}



