/* Image Slideshow */
.image-slideshow-container {
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;


}

.image-slideshow-container img{
  vertical-align: middle;
  width: 100%;
  border-radius: 20px;
  /* box-shadow:  5px 0px 5px 0px rgb(81, 81, 81); */
  text-align: center;


}
.image-slideshow-caption p{
  font-size: 1.25rem;
  margin: 10px;
  text-align: center;
  font-weight: bolder;

}
.image-slideshow-btn {
  width: 40px;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background-color: rgba(202, 202, 202, 0.7);
  /* box-shadow:  5px 0px 5px 0px rgb(81, 81, 81); */
  font-size: 18px;
  z-index: 2;
  margin: 10px;
}
.image-slideshow-btn:active {
  transform: scale(1.1);
}