@media (max-width: 767px) {

    .section {
        margin: 10px 15px;
    }

    .section-heading h1 {
        font-size: 26px;

    }

    .banner-content h1 {
        font-size: 44px;
    }

    .banner-content h2 {
        font-size: 18px;

    }

    .footer-nav {
        font-size: 12px;
        padding: 10px;

    }

    .grid-2 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;
        margin-bottom: 2.5rem;
    }

    .grid-4 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;

    }

    .grid-1-3 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;
    }

    .grid-3-1 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;
    }

    .grid-1-2 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;
    }

    .grid-3 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;

    }


    .image-slideshow img {
        width: 100%;
        border-radius: 10px;
        box-shadow: none;

    }

    .image-slideshow-btn {
        display: none;
    }

    .image-slideshow h1 {
        font-size: 18px;
        margin: 5px;

    }

    .image-slideshow-caption p {
        font-size: 1rem;
        margin: 10px;
        text-align: center;

    }
    .footer-container {
        margin: 0px 20px;
    }

    .footer-cta {
        padding: 10px 0px;
    }

    .footer-content {
        padding: 10px 0px;
    }

    .footer-logo {
        margin-bottom: 10px;
        text-align: center;
    }

    .footer-logo img {
        max-width: 100px;
    }

    .footer-text p {
        display: none;
    }
    .card-item-btn{
        padding: 10px;
        font-size: 14px;
        margin: 10px 0px;
      }


    .footer-widget-heading h3 {
        font-size: 16px;
        font-weight: 600;
        margin-bottom: 40px;
        position: relative;
    }

    .footer-widget ul li {
        display: inline-block;
        float: left;
        width: 50%;
        margin-bottom: 10px;
    }

    .footer-widget ul li a {
        font-size: 14px;
    }

    .copyright-area {
        padding: 10px 10px;

    }

    .copyright-text p {
        font-size: 14px;
        text-align: center;
    }

    .footer-menu {
        text-align: center;

    }

    .footer-menu li {
        margin-left: 15px;

    }

    .footer-menu li a {
        font-size: 14px;
    }

    #head {
        padding: 0 0;
    }

    .card-item {
        
        border-radius: 15px;
        min-height: 300px;
        padding: 1rem;
    }

    .card-item-img img {
        border-radius: 15px;

    }


    .para-content {
        margin-bottom: 15px;
    }

    .para-content p {
        font-size: 16px;
    }

    .para-content a {
        font-size: 16px;

    }

    .para-content ol {
        width: 100%;
        margin: 0px 15px;
    }

    .para-content ol li {
        font-size: 16px;
    }

    .about-section p {
        font-size: 16px;
        text-align: left;
    }


    .scroll-to-top {
        font-size: 25px;
        width: 45px;
        height: 45px;
        text-align: center;
        border-radius: 50px;
        padding: 10px;
        position: fixed;
        bottom: 30px;
        right: 30px;
        z-index: 1000;
        cursor: pointer;
        display: none;
        box-shadow: 0 0 11px rgba(33, 33, 33, .5);

    }

    .visible {
        display: block;
        transition: right 0.2s ease-in-out;
    }


}





