.preloader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.spinner {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 10px solid #0c75ff;
  border-top-color: #d2d2d2;
  animation: spin 1s ease-in-out infinite;
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.30);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
